<template>
  <div style="position:relative;padding:20px;">
    <Spin fix v-show='loading'>
      <Icon type="load-c" size=18 class="demo-spin-icon-load"></Icon>
      <div>正在获取数据...</div>
    </Spin>
    <Form ref="cateDetial" :model="formData" :rules="rules" label-position="left" :label-width="120">
       <FormItem label="父级分类：" prop='parentid'>
        <Row>
          <Col span='10'>
          <Input v-model='parentData.catename' :disabled='true'></Input>
          </Col>
        </Row>
      </FormItem>
      <FormItem label="分类名称：" prop='catename'>
        <Input v-model='formData.catename' placeholder='请输入分类名称'></Input>
      </FormItem>
      <FormItem label="显示顺序：" prop='sortvalue'>
        <Row>
          <Col span='6'>
            <InputNumber v-model='formData.sortvalue' :min='0'></InputNumber>
          </Col>
          <Col span='10'>
            <span style="color:#ccc">数值越大，在父级分类中的顺序越靠前</span>
          </Col>
        </Row>
      </FormItem>
      <FormItem label="分类图片：" prop='catepic'>
        <single-uploader :image='formData.catepic' @change='handleImgChange'></single-uploader>
      </FormItem>
      <FormItem label="首页推荐" prop="isrecommend">
        <i-Switch size="large" v-model="formData.isrecommend" :true-value='1' :false-value='0'>
          <span slot="open">是</span>
          <span slot="close">否</span>
        </i-Switch>
      </FormItem>
      <FormItem label="是否显示" prop="ishidden">
        <Row>
          <Col :span='6'>
            <i-Switch size="large" v-model="formData.ishidden" :true-value='0' :false-value='1'>
              <span slot="open">是</span>
              <span slot="close">否</span>
            </i-Switch>
          </Col>
          <Col :span='18'>
            <span style="color:#ccc">用于控制这个分类在小程序端的显示与否，请慎重选择</span>
          </Col>
        </Row>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleAdd('cateDetial')" size="large" style="width:120px">保存</Button>
        <Button type="ghost" @click="handleReset" size="large" style="margin-left: 8px;width:80px">取消</Button>
      </FormItem>

    </Form>
  </div>
</template>
<script>
import singleImgUploader from '../common/SingleImgUploader.vue'
export default {
  data() {
    return {
      formData: {
        catename: '',
        catepic: 'http://pic.meisaas.com/wechat/sales/summer_special.png',
        sortvalue: 0,
        isrecommend: 0,
        parentid: '',
        ishidden:0,
      },
      rules: {
        catename: { required: true, message: '分类名称不能为空！', trigger: 'blur' },
        catepic: { required: true, message: '分类图片不能为空！', trigger: 'blur' },
      },
      loading: false,
      parentData: {
        id:0,
        catename:''
      },
    }
  },
  methods: {
    handleImgChange(file) {
      this.formData.catepic = file.url
    },
    handleAdd(name) {
      this.$refs[name].validate(valid=>{
        if(valid) {
          return this.$http.post(this.$api.category_add, this.formData).then(res => {
            this.$Message.success('添加成功！')
            this.$router.push('/category/list')
            // this.$router.push(`/category/edit?cateid=${res.data.data}`)
          }).catch(erro => {
            this.$Message.error('添加失败！')
          })
        }else {
          this.$Message.error('请检查数据是否填充完整！')
        }
      })
    },
    handleReset() {
      this.$router.push('/category/list')
    },
    getCategoryDetail(id) {
      this.loading = true;
      return this.$http.post(this.$api.category_detail, { cateid: id }).then(res => {
        Object.keys(this.formData).forEach(key => {
          this.formData[key] = res.data.data[key];
        });
        let pid = res.data.data.parentid;
        if (pid == 0) {
          this.parentData = {
            id: 0,
            catename: '根级分类'
          };
        } else {
          this.$http.post(this.$api.category_detail, { cateid: pid }).then(res => {
            this.parentData = res.data.data;
          }).catch(err => {});
        }

      }).catch(erro => {
      }).then(() => {
        this.loading = false;
      });
    },
  },
  components: {
    'single-uploader': singleImgUploader
  },
  mounted() {
    this.$sitemap.prvname = "商品分类";
    this.$sitemap.currname = '添加分类';
   this.formData.parentid = this.$route.query.parentid
   this.parentData = {
      id:this.$route.query.parentid,
      catename:this.$route.query.parentname
   }

  },
}

</script>
<style>
.edit-parentcate {
  color: #3bc0c3;
  cursor: pointer;
}

.edit-parentcate:hover {
  color: #666
}

</style>



